.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 30%;
		left: 0;
		width: 180px;
		transform: translateX(-30%);
		opacity: 0.8;
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 120px;
		}
		@include max-xs {
			width: 100px;
		}
		img {
			transform-origin: 10% 80%;
		}
		.orn-2 {
			position: absolute;
			top: 0;
			left: 0;
			width: 140px;
			transform: translateY(-60%);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
			img {
				transform-origin: 50% 50%;
			}
		}
		.orn-3 {
			position: absolute;
			top: 0;
			left: 0;
			width: 140px;
			transform: translate(20%, -10%);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
			img {
				transform-origin: 10% 100%;
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 35%;
		right: 0;
		width: 180px;
		transform: translate(30%, -50%);
		@include max-lg {
			width: 140px;
		}
		@include max-md {
			width: 120px;
		}
		@include max-xs {
			width: 100px;
		}
		img {
			transform-origin: 100% 50%;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 320px;
		transform: translate(-10%, 5%) scaleY(-1);
		opacity: 0.8;
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 10% 0%;
		}
	}
	.orn-6 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 320px;
		transform: translate(10%, 5%) scale(-1);
		opacity: 0.8;
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 10% 0%;
		}
	}
	.image-wrapper {
		position: relative;
		width: 70%;
		height: auto;
		aspect-ratio: 3/4;
		margin: 0 auto;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			padding: 4px;
			background: $stroke-gradient;
			mask-image: url(../images/mask-1.png);
			mask-size: 100% 100%;
			mask-repeat: no-repeat;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				mask-image: url(../images/mask-1.png);
				mask-size: 100% 100%;
				mask-repeat: no-repeat;
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
			&::before {
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				height: 60%;
				background-color: var(--color-primary);
				border-radius: var(--border-radius);
				content: "";
			}
			.thank-body {
				position: relative;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
