.couple-section {
	position: relative;
	padding: 0;
	text-align: center;
	overflow: hidden;
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 320px;
		transform: translate(-10%, 5%) scaleY(-1);
		opacity: 0.8;
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 15% 0%;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 320px;
		transform: translate(10%, 5%) scale(-1);
		opacity: 0.8;
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 15% 0%;
		}
	}
	.orn-5 {
		position: absolute;
		top: 20%;
		left: 0;
		width: 240px;
		transform: translateX(-30%) scaleX(-1);
		opacity: 0.8;
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 100% 50%;
		}
		.orn-6 {
			position: absolute;
			top: 0;
			right: 0;
			width: 160px;
			transform: scaleX(-1);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 100px;
			}
			img {
				transform-origin: 50% 50%;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 28%;
		right: 0;
		width: 180px;
		transform: translateX(20%) scaleX(-1);
		opacity: 0.8;
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 0% 100%;
		}
		.orn-8 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 140px;
			transform: translate(-10%, 30%);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
			img {
				transform-origin: 50% 50%;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		.image-element {
			width: 100%;
			height: 100%;
			background: $stroke-gradient;
			padding: 4px;
			mask-image: url(../images/mask-1.png);
			mask-size: 100% 100%;
			mask-repeat: no-repeat;
			.couple-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				mask-image: url(../images/mask-1.png);
				mask-size: 100% 100%;
				mask-repeat: no-repeat;
			}
		}
	}
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		color: var(--color-secondary);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		margin-bottom: 1rem;
		z-index: 3;
		span {
			position: relative;
			font-size: 48px;
			font-family: $font-secondary;
		}
	}
	.couple-body {
		position: relative;
		padding: 3rem 2rem;
		border-radius: var(--border-radius);
		background: linear-gradient(180deg, rgba(255, 255, 255, 0.8), transparent);
	}
	.couple-content {
		position: relative;
	}
	.couple {
		position: relative;
		text-align: center;
		.image-wrap {
			width: 80%;
			position: relative;
			margin: 0 auto;
			aspect-ratio: 3/4;
			z-index: 2;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				background: $stroke-gradient;
				padding: 4px;
				mask-image: url(../images/mask-1.png);
				mask-size: 100% 100%;
				mask-repeat: no-repeat;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					mask-image: url(../images/mask-1.png);
					mask-size: 100% 100%;
					mask-repeat: no-repeat;
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 2rem 1rem 3rem;
			background: linear-gradient(180deg, var(--color-primary), transparent);
			border-radius: var(--border-radius);
			margin-top: -1rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-dark);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
