.gallery-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.img-gallery {
		height: auto;
		width: 100%;
		aspect-ratio: 7/8;
		object-fit: cover;
		margin-bottom: 0.75rem;
	}
	.splide {
		.splide__arrow {
			border-radius: 100px;
			width: 70px;
			height: 70px;
			background-color: var(--color-secondary);
			@include max-lg {
				width: 40px;
				height: 40px;
			}
			svg {
				fill: var(--text-button);
				width: 25px;
				height: 30px;
				@include max-md {
					width: 15px;
					height: 20px;
				}
			}
			&--prev {
				left: 0;
				transform: translate(-50%, calc(-50% - 0.5rem));
			}
			&--next {
				right: 0;
				transform: translate(50%, calc(-50% - 0.5rem));
			}
		}
	}
}
