.reminder-wrap {
	position: relative;
	display: block;
	background-color: rgba($color: #fff, $alpha: 0.5);
	padding: 5rem 1rem 0;
	@include max-md {
		padding: 3rem 0.75rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 320px;
		transform: translate(-10%, -3%);
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 10% 0%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 320px;
		transform: translate(10%, -3%) scaleX(-1);
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 10% 0%;
		}
	}
	.reminder-content {
		position: relative;
		z-index: 3;
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		transform: translateY(50%);
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
