.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.appology-wrap {
		position: relative;
		background: linear-gradient(180deg, var(--color-custom-main), transparent);
		border-radius: var(--border-radius);
		color: var(--color-light);
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
