.story-section {
	position: relative;
	padding: 5rem 0;
	background-color: rgba($color: #fff, $alpha: 0.5);
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 200px;
		transform: translateX(-30%);
		opacity: 0.8;
		@include max-lg {
			top: 30%;
			width: 160px;
		}
		@include max-md {
			top: 40%;
			width: 140px;
		}
		@include max-xs {
			top: 35%;
			width: 100px;
		}
		img {
			transform-origin: 10% 80%;
		}
		.orn-2 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translateY(-60%);
			@include max-lg {
				width: 120px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
			img {
				transform-origin: 50% 50%;
			}
		}
		.orn-3 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translate(20%, -10%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
			img {
				transform-origin: 10% 80%;
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 200px;
		transform: translate(30%, -50%);
		@include max-lg {
			top: 35%;
			width: 180px;
		}
		@include max-md {
			top: 40%;
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 100% 50%;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
		padding: 3rem 0 0;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 3/4;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			padding: 4px;
			background: $stroke-gradient;
			mask-image: url(../images/mask-1.png);
			mask-size: 100% 100%;
			mask-repeat: no-repeat;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
				mask-image: url(../images/mask-1.png);
				mask-size: 100% 100%;
				mask-repeat: no-repeat;
			}
		}
	}
}
