.wishes-section {
	position: relative;
	padding: 0 0 5rem;
	@include max-md {
		padding: 0 0 3rem;
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		right: 0;
		width: 200px;
		transform: translate(30%, -10%) scaleX(-1);
		opacity: 0.8;
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			top: 32%;
			width: 120px;
		}
		@include max-xs {
			width: 100px;
		}
		img {
			transform-origin: 10% 100%;
		}
		.orn-2 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translateY(-60%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
			img {
				transform-origin: 50% 50%;
			}
		}
		.orn-3 {
			position: absolute;
			top: 0;
			left: 0;
			width: 160px;
			transform: translate(20%, -10%);
			@include max-lg {
				width: 140px;
			}
			@include max-md {
				width: 100px;
			}
			@include max-xs {
				width: 80px;
			}
			img {
				transform-origin: 10% 80%;
			}
		}
	}
	.orn-4 {
		position: absolute;
		top: 50%;
		left: 0;
		width: 200px;
		transform: translate(-30%, -50%) scaleX(-1);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 120px;
		}
		@include max-xs {
			width: 100px;
		}
		img {
			transform-origin: 100% 50%;
		}
	}
	.wishes-form-content {
		position: relative;
		background-color: rgba($color: #fff, $alpha: 0.6);
		padding-top: 5rem;
		@include max-md {
			padding-top: 3rem;
		}
	}
	.card-form {
		margin-bottom: 0;
		background-color: transparent;
		position: relative;
		z-index: 3;
		&.card {
			border: none;
		}
		.card-body {
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}

	.wishes-preview {
		position: relative;
		.card {
			border: none;
			position: relative;
			background-color: transparent;
			border-radius: var(--border-radius);
			.card-body {
				overflow: hidden;
				padding: 2rem 0;
				.wishes {
					text-align: left;
					height: 28rem;
					overflow: auto;
					padding-right: 1rem;
					&::-webkit-scrollbar {
						width: 5px;
					}
					&::-webkit-scrollbar-track {
						background-color: #eee;
						border-radius: 20px;
						opacity: 0.15;
					}
					&::-webkit-scrollbar-thumb {
						background-color: var(--color-secondary);
						border-radius: 20px;
					}
					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						display: grid;
						grid-template-columns: auto 1fr;
						gap: 1rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
						.wish-badge {
							background-color: var(--color-light);
							width: 35px;
							aspect-ratio: 1;
							display: flex;
							justify-content: center;
							align-items: center;
							border-radius: 50px;
						}
						.wish-description {
							padding: 1rem;
							background-color: rgba($color: #fff, $alpha: 0.6);
							border-radius: 5px;
							position: relative;
							&::before {
								position: absolute;
								top: 15px;
								left: 0;
								width: 15px;
								height: 15px;
								transform: translateX(-100%);
								clip-path: url(#wishes-polygon);
								content: "";
								background-color: rgba($color: #fff, $alpha: 0.6);
							}
						}
					}
				}
			}
		}
	}
}
