.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	overflow: hidden;
	.scroll-icon {
		position: relative;
		text-align: center;
		margin: 1rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 320px;
		transform: translate(-10%, -10%);
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 10% 0%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 320px;
		transform: translate(10%, -10%) scaleX(-1);
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 10% 0%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 180px;
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 120px;
		}
		@include max-xs {
			width: 80px;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 160px;
		transform: translateY(10%) scaleX(-1);
		@include max-lg {
			width: 120px;
		}
		@include max-md {
			width: 100px;
		}
		@include max-xs {
			width: 60px;
		}
	}
	.header-content {
		height: 100%;
		@include max-xl {
			display: flex;
			align-items: center;
			justify-content: center;
		}
		.header {
			width: fit-content;
			min-width: 40%;
			max-width: 100%;
			height: 100%;
			margin: 0 auto;
			padding: 4.5rem 0 3rem;
			@include max-xl {
				min-width: 70%;
				min-height: 80%;
				height: 0;
				margin: auto;
			}
			@include max-lg {
				min-width: 80%;
			}
			@include max-md {
				min-width: 100%;
				min-height: 80%;
			}
			@include max-cwh(375px, 680px) {
				min-height: 100%;
			}
			.header-title {
				position: relative;
				width: 100%;
				background: $stroke-gradient;
				display: flex;
				justify-content: center;
				align-items: center;
				height: 100%;
				text-align: center;
				mask-image: url(../images/mask-1.png);
				mask-size: 100% 100%;
				mask-repeat: no-repeat;
				padding: 3rem 2rem;
				@include max-xl {
					padding: 5rem 2rem;
				}
				&::before {
					position: absolute;
					top: 4px;
					right: 4px;
					bottom: 4px;
					left: 4px;
					background-color: rgba($color: #fff, $alpha: 1);
					mask-image: url(../images/mask-1.png);
					mask-size: 100% 100%;
					mask-repeat: no-repeat;
					content: "";
				}
				&-content {
					position: relative;
					z-index: 5;
					color: #987c1b;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes zoomInMain {
	0% {
		transform: scaleZ(1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
