.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 320px;
		transform: translate(-10%, -5%);
		opacity: 0.8;
		@include max-lg {
			width: 280px;
		}
		@include max-md {
			width: 220px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 10% 0%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 320px;
		transform: translate(10%, -5%) scaleX(-1);
		opacity: 0.8;
		@include max-lg {
			width: 280px;
		}
		@include max-md {
			width: 220px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 10% 0%;
		}
	}
	.stream-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		background: linear-gradient(180deg, var(--color-custom-main), transparent);
		padding: 3rem 2rem;
		z-index: 5;
		@include max-lg {
			.row {
				align-items: center;
				gap: 2rem;
			}
		}
		@include max-md {
			padding: 2rem 1rem;
		}
		.title-section {
			h2 {
				color: var(--color-light);
			}
		}
		.image-wrapper {
			position: relative;
			width: 100%;
			height: auto;
			aspect-ratio: 3/4;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				padding: 4px;
				background: $stroke-gradient;
				mask-image: url(../images/mask-1.png);
				mask-size: 100% 100%;
				mask-repeat: no-repeat;
				.stream-background {
					position: relative;
					object-fit: cover;
					width: 100%;
					height: 100%;
					mask-image: url(../images/mask-1.png);
					mask-size: 100% 100%;
					mask-repeat: no-repeat;
				}
			}
		}
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				padding: 3rem 2rem;
				background: var(--color-gradient);
				border-radius: var(--border-radius);
				@include max-md {
					padding: 2rem 1rem;
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		width: 100%;
		margin-top: 3rem;
		@include max-md {
			text-align: center;
		}
	}
}
