.rsvp-section {
	position: relative;
	padding: 5rem 0;
	overflow: hidden;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 320px;
		transform: translate(-10%, -5%);
		opacity: 0.8;
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 220px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 10% 0%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 320px;
		transform: translate(10%, -5%) scaleX(-1);
		opacity: 0.8;
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 220px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 10% 0%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 320px;
		transform: translate(-10%, 5%) scaleY(-1);
		opacity: 0.8;
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 220px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 10% 0%;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 320px;
		transform: translate(10%, 5%) scale(-1);
		opacity: 0.8;
		@include max-lg {
			width: 260px;
		}
		@include max-md {
			width: 220px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 10% 0%;
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: 3rem 2rem;
		border-radius: var(--border-radius);
		background: linear-gradient(180deg, var(--color-primary), transparent);
		@include max-md {
			padding: 2rem 1rem;
		}
		.rsvp-form {
			position: relative;
			.title-section {
				&::before {
					all: unset;
				}
			}
			.form-control,
			.form-select {
				background-color: rgba($color: #fff, $alpha: 0.7);
			}
		}
	}
}
