.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 240px;
		transform: translate(-20%, -20%);
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 10% 0%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 240px;
		transform: translate(20%, -20%) scaleX(-1);
		@include max-md {
			width: 200px;
		}
		@include max-xs {
			width: 180px;
		}
		img {
			transform-origin: 10% 0%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 50%;
		transform: translate(-30%, 50%);
		@include max-lg {
			width: 80%;
			transform: translate(-60%, 50%);
		}
		@include max-md {
			width: 100%;
			transform: translate(-60%, 40%);
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 50%;
		transform: translate(30%, 50%) scaleX(-1);
		@include max-lg {
			width: 80%;
			transform: translate(60%, 50%) scaleX(-1);
		}
		@include max-md {
			width: 100%;
			transform: translate(60%, 40%) scaleX(-1);
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		@include max-lg {
			padding: 4rem 0 8rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			color: var(--color-dark);
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			.cover-body {
				position: relative;
				padding: 5rem 2rem;
				width: 38%;
				max-width: 100%;
				min-height: 90%;
				display: flex;
				align-items: center;
				justify-content: center;
				background: $stroke-gradient;
				mask-image: url(../images/mask-1.png);
				mask-size: 100% 100%;
				mask-repeat: no-repeat;
				&::before {
					position: absolute;
					top: 5px;
					left: 5px;
					right: 5px;
					bottom: 5px;
					background-color: var(--color-primary);
					mask-image: url(../images/mask-1.png);
					mask-size: 100% 100%;
					mask-repeat: no-repeat;
					content: "";
				}
				@include max-lg {
					min-width: 70%;
				}
				@include max-md {
					width: 100%;
					min-height: 80%;
					padding: 5rem 1rem;
				}
				.cover-description {
					padding: 0 2rem;
					text-align: center;
					@include max-lg {
						padding: 2rem 0 1rem;
					}
					.greeting-wrapper {
						position: relative;
						@include max-md {
							font-size: 14px;
						}
						h6 {
							font-weight: 700;
						}
					}
					h6 {
						font-weight: 600;
					}
					.title {
						color: var(--color-dark);
					}
				}
			}
			.widget-element {
				width: 100%;
			}
		}
	}
}
